// Buttons Style

.btn {
  width: 200px;
  display: block;
  margin: 10px;
  padding: 5px;
  font-family: "Oswald Regular", sans-serif;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  text-transform: capitalize;
  background: $dark-blue;
  color: white;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background: darken($dark-blue, 20%); }

  &_cancel {
    background: $raspberry;

    &:hover {
      background: darken($raspberry, 20%); } }

  &:active {
    outline: 0; } }



// Fields Style


.field {
  display: block;
  width: 100%;
  height: 30px;
  margin: 10px auto;
  border: none;
  box-sizing: border-box;
  padding-left: 5px;
  font-size: 16px;

  &:focus {
    outline: none;
    border-bottom: 2px solid $sharp-blue; } }

//Forms

.form-title {
  margin: 0 0 20px 0;
  padding: 0;
  font-size: 25px;
  text-transform: uppercase; }

.form-label {
  display: block;
  text-transform: capitalize;
  font-size: 16px;

  &_required:after {
    content: ' *';
    display: inline;
    color: red; } }

.ng-submitted .ng-invalid {
  border: 1px solid red; }

.field-error {
  display: block;
  margin: 0 auto 10px;
  font-size: 13px;
  color: red;

  &_center {
    text-align: center; } }


// Pop-Up Styles

.pop-up-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  z-index: 1;
  background: rgba(51, 51, 51, 0.35); }

.pop-up {
  position: relative;
  width: 450px;
  background: white;

  @media ($mxw:$sdmx) {
    width: 300px; }
  &__title {
    width: 100%;
    box-sizing: border-box;
    font-size: 18px;
    font-weight: normal;
    background: $light-grey;
    padding: 15px;
    margin: 0; }

  &__notification {
    display: block;
    margin: 10px;
    text-align: center;
    &_light {
      font-family: "Oswald Light", sans-serif; }

    &-user {
      overflow-wrap: break-word;
      hyphens: auto;
      font-family: "Oswald Bold"; } }

  &__button {
    margin: 10px auto 15px; }

  &-button-wrapper {
    display: flex;
    justify-content: space-around;
    margin: 15px; }

  &__tip {
    width: 100%;
    box-sizing: border-box;
    background: $light-grey;
    text-align: center;
    font-family: Oswald Light;
    font-size: 14px;
    padding: 5px 0; }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    border: 0;
    background-color: transparent;
    background-image: url("assets/img/close-icon.svg");
    background-position: center;
    background-repeat: no-repeat;

    &:active {
      outline: none; } } }



